body{background-color:rgba(0,0,0,0.85);font-family:'Fira Mono';padding:5em;margin:0}h1{display:block;border:2px solid white;width:3em;height:3em;padding:0;margin:0;position:relative}h1 i{font-family:'Roboto Slab';font-style:normal !important;display:inline;position:absolute;bottom:0;left:0;color:white;font-weight:400;padding:0.45em 0.6em}main{color:#ffffffbb;position:relative}main nav{display:inline-block;padding-left:1.2em;margin-top:0.5em}main nav a{color:#ffffff99;text-decoration:none;margin-right:1.5em;text-decoration:underline;text-decoration-style:dotted;transition:color 0.1s ease-in-out}main nav a:hover{color:#ffffffff}main p.intro{position:absolute;display:block;padding-left:1.2em;font-size:1.1em;line-height:2em;height:9.5em}main p.intro span.cursor{background-color:white;display:inline-block;width:0.65em;height:1.25em;vertical-align:middle;animation:0.75s blink step-end infinite}main p.intro.shadow{padding-left:1.2em;font-size:1.1em;padding-top:1em;line-height:2em;height:9.5em;position:relative;display:block;opacity:0}article{display:flex;flex-direction:row;flex-wrap:wrap;margin-left:-5em;width:calc(100% + 10em);background-color:#00fdd6;padding:6em;box-sizing:border-box}article:first-of-type{margin-top:5em}article img{flex-grow:1;text-align:center;padding:0 10em;width:20em}article img.neutrino{content:url(8a9879243cd2152f9784097d467bd92a.png)}article:hover img.neutrino{content:url(9d41fe4ccc5891c39b3c6f6556465647.png)}article div.description{flex-grow:1;display:inline-flex;justify-content:center;flex-direction:column}article div.description h2{margin:0;padding:0;font-size:1em;font-family:'Roboto slab', sans-serif}article div.description span.text{margin-top:1em;display:block}article div.description span.links{margin-top:1em;display:block}article div.description span.links a{display:inline-block;height:3.3em;padding:1em 1em 1em 3.5em;vertical-align:top;background-color:#00000030;border-radius:3px;text-decoration:none;color:#000000fb;box-sizing:border-box;position:relative;transition:all 0.1s ease-in-out}article div.description span.links a:hover{transform:translateY(-4px);box-shadow:0 4px 0 #00000030;background-color:#00000020}article div.description span.links a i{position:absolute;left:0.7em;top:0.55em}@keyframes blink{from,to{opacity:0}50%{opacity:100%}}
